___CSS_LOADER_EXPORT___.push([module.id, `/* Hamburger Menu Button */
editor-menu .hamburger-menu {
    position: fixed;
    top: 0.625rem;
    right: 0.625rem;
    z-index: 100;
    width: 2.5rem;
    height: 2.5rem;
    padding: 0.5rem;
    background-color: var(--lnsy-edit-background);
    border: 0.0625rem solid var(--lnsy-edit-gutter-border);
    border-radius: 0.25rem;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    transition: background-color 0.2s ease;
}

editor-menu .hamburger-menu:hover {
    background-color: var(--lnsy-edit-selection);
}

editor-menu .hamburger-menu span {
    display: block;
    width: 100%;
    height: 0.125rem;
    background-color: var(--lnsy-edit-foreground);
    transition: transform 0.2s ease;
}

/* Command Palette Dialog */
editor-menu .command-palette {
    position: fixed;
    background-color: var(--lnsy-edit-background);
    border: 0.0625rem solid var(--lnsy-edit-gutter-border);
    border-radius: 0.5rem;
    box-shadow: 0 0.625rem 2.5rem rgba(0, 0, 0, 0.3);
    overflow: hidden;
}

editor-menu .command-palette::backdrop {
    background-color: rgba(0, 0, 0, 0.5);
}

/* Command Palette Header */
editor-menu .command-palette-header {
    padding: 0.75rem;
    border-bottom: 0.0625rem solid var(--lnsy-edit-gutter-border);
    background-color: var(--lnsy-edit-gutter-background);
}

editor-menu .command-search {
    width: 100%;
    padding: 0.5rem 0.75rem;
    font-size: 1rem;
    background-color: var(--lnsy-edit-background);
    color: var(--lnsy-edit-foreground);
    border: 0.0625rem solid var(--lnsy-edit-gutter-border);
    border-radius: 0.25rem;
    outline: none;
}

editor-menu .command-search:focus {
    border-color: var(--lnsy-edit-selection);
    box-shadow: 0 0 0 0.125rem
        rgba(var(--lnsy-edit-selection-rgb, 100, 100, 100), 0.2);
}

/* Command Palette Body */
editor-menu .command-palette-body {
    max-height: 18.75rem;
    min-width: 40em;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 1em;
}

/* Command List */
editor-menu .command-list {
    list-style: none;
    margin: 0;
    padding: 0.5rem 0;
}

editor-menu .command-list li {
    padding: 0.5rem 0.75rem;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: background-color 0.1s ease;
}

editor-menu .command-list li:not(.category-header):not(.no-results):hover,
editor-menu .command-list li.selected {
    background-color: var(--lnsy-edit-selection);
}

editor-menu .command-list li:focus {
    outline: none;
    background-color: var(--lnsy-edit-selection);
}

/* Category Headers */
editor-menu .category-header {
    padding: 0.25rem 0.75rem !important;
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
    color: var(--lnsy-edit-comment, #6c757d);
    cursor: default !important;
    background-color: transparent !important;
    border-bottom: 0.0625rem solid var(--lnsy-edit-gutter-border);
    margin-top: 0.25rem;
}

editor-menu .category-header:first-child {
    margin-top: 0;
}

/* Command Items */
editor-menu .command-label {
    flex: 1;
    color: var(--lnsy-edit-foreground);
}

editor-menu .command-shortcut {
    margin-left: 1rem;
    padding: 0.125rem 0.375rem;
    font-size: 0.75rem;
    color: var(--lnsy-edit-comment, #6c757d);
    background-color: var(--lnsy-edit-gutter-background);
    border-radius: 0.25rem;
    font-family: monospace;
}

/* No Results */
editor-menu .no-results {
    padding: 2rem !important;
    text-align: center;
    color: var(--lnsy-edit-comment, #6c757d);
    font-style: italic;
    cursor: default !important;
    background-color: transparent !important;
}

/* Scrollbar Styling */
editor-menu .command-palette-body::-webkit-scrollbar {
    width: 0.5rem;
}

editor-menu .command-palette-body::-webkit-scrollbar-track {
    background: var(--lnsy-edit-gutter-background);
}

editor-menu .command-palette-body::-webkit-scrollbar-thumb {
    background: var(--lnsy-edit-gutter-border);
    border-radius: 0.25rem;
}

editor-menu .command-palette-body::-webkit-scrollbar-thumb:hover {
    background: var(--lnsy-edit-selection);
}

/* Mobile Adjustments */
@media (max-width: 48rem) {
    editor-menu .command-palette {
        width: 95vw;
        max-height: 80vh;
    }

    editor-menu .command-palette-body {
        max-height: calc(80vh - 4rem);
    }

    editor-menu .hamburger-menu {
        top: 0.375rem;
        left: 0.375rem;
    }
}

/* Animation for dialog */
editor-menu .command-palette[open] {
    animation: slideIn 0.2s ease-out;
}

@keyframes slideIn {
    from {
        opacity: 0;
        transform: translate(-50%, -48%);
    }
    to {
        opacity: 1;
        transform: translate(-50%, -50%);
    }
}
