___CSS_LOADER_EXPORT___.push([module.id, `/* Based on the default CodeMirror theme */

.cm-s-lnsy-edit.CodeMirror {
  background: var(--lnsy-edit-background);
  color: var(--lnsy-edit-foreground);
}

.cm-s-lnsy-edit div.CodeMirror-selected {
  background: var(--lnsy-edit-selection);
}

.cm-s-lnsy-edit .CodeMirror-gutters {
  background: var(--lnsy-edit-gutter-background);
  border-right: 1px solid var(--lnsy-edit-gutter-border);
}

.cm-s-lnsy-edit .CodeMirror-guttermarker {
  color: var(--lnsy-edit-guttermarker);
}

.cm-s-lnsy-edit .CodeMirror-guttermarker-subtle {
  color: var(--lnsy-edit-guttermarker-subtle);
}

.cm-s-lnsy-edit .CodeMirror-linenumber {
  color: var(--lnsy-edit-linenumber);
}

.cm-s-lnsy-edit .CodeMirror-cursor {
  border-left: 1px solid var(--lnsy-edit-cursor);
}

.cm-s-lnsy-edit span.cm-comment {
  color: var(--lnsy-edit-comment);
}

.cm-s-lnsy-edit span.cm-atom {
  color: var(--lnsy-edit-atom);
}

.cm-s-lnsy-edit span.cm-number {
  color: var(--lnsy-edit-number);
}

.cm-s-lnsy-edit span.cm-property,
.cm-s-lnsy-edit span.cm-attribute {
  color: var(--lnsy-edit-property);
}

.cm-s-lnsy-edit span.cm-keyword {
  color: var(--lnsy-edit-keyword);
}

.cm-s-lnsy-edit span.cm-string {
  color: var(--lnsy-edit-string);
}

.cm-s-lnsy-edit span.cm-variable {
  color: var(--lnsy-edit-variable);
}

.cm-s-lnsy-edit span.cm-variable-2 {
  color: var(--lnsy-edit-variable-2);
}

.cm-s-lnsy-edit span.cm-def {
  color: var(--lnsy-edit-def);
}

.cm-s-lnsy-edit span.cm-bracket {
  color: var(--lnsy-edit-bracket);
}

.cm-s-lnsy-edit span.cm-tag {
  color: var(--lnsy-edit-tag);
}

.cm-s-lnsy-edit span.cm-link {
  color: var(--lnsy-edit-link);
}

.cm-s-lnsy-edit span.cm-error {
  background: var(--lnsy-edit-error-background);
  color: var(--lnsy-edit-error-foreground);
}

.cm-s-lnsy-edit .CodeMirror-activeline-background {
  background: var(--lnsy-edit-activeline-background);
}

.cm-s-lnsy-edit .CodeMirror-matchingbracket {
  color: var(--lnsy-edit-matchingbracket-color) !important;
  background: var(--lnsy-edit-matchingbracket-background);
}

.ci {
  color: var(--lnsy-edit-tag) !important;
}

lnsy-edit {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.editor-container {
  flex-grow: 1;
  overflow-y: auto;
  height: 100%;
}

.cm-editor {
    height: 100%;
}`, "",{"version":3,"sources":["webpack://./styles/lnsy-edit.css"],"names":[],"mappings":"AAAA,0CAA0C;;AAE1C;EACE,uCAAuC;EACvC,kCAAkC;AACpC;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,8CAA8C;EAC9C,sDAAsD;AACxD;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,2CAA2C;AAC7C;;AAEA;EACE,kCAAkC;AACpC;;AAEA;EACE,8CAA8C;AAChD;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,8BAA8B;AAChC;;AAEA;;EAEE,gCAAgC;AAClC;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,kCAAkC;AACpC;;AAEA;EACE,2BAA2B;AAC7B;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,2BAA2B;AAC7B;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,6CAA6C;EAC7C,wCAAwC;AAC1C;;AAEA;EACE,kDAAkD;AACpD;;AAEA;EACE,wDAAwD;EACxD,uDAAuD;AACzD;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,gBAAgB;EAChB,YAAY;AACd;;AAEA;IACI,YAAY;AAChB","sourcesContent":["/* Based on the default CodeMirror theme */\n\n.cm-s-lnsy-edit.CodeMirror {\n  background: var(--lnsy-edit-background);\n  color: var(--lnsy-edit-foreground);\n}\n\n.cm-s-lnsy-edit div.CodeMirror-selected {\n  background: var(--lnsy-edit-selection);\n}\n\n.cm-s-lnsy-edit .CodeMirror-gutters {\n  background: var(--lnsy-edit-gutter-background);\n  border-right: 1px solid var(--lnsy-edit-gutter-border);\n}\n\n.cm-s-lnsy-edit .CodeMirror-guttermarker {\n  color: var(--lnsy-edit-guttermarker);\n}\n\n.cm-s-lnsy-edit .CodeMirror-guttermarker-subtle {\n  color: var(--lnsy-edit-guttermarker-subtle);\n}\n\n.cm-s-lnsy-edit .CodeMirror-linenumber {\n  color: var(--lnsy-edit-linenumber);\n}\n\n.cm-s-lnsy-edit .CodeMirror-cursor {\n  border-left: 1px solid var(--lnsy-edit-cursor);\n}\n\n.cm-s-lnsy-edit span.cm-comment {\n  color: var(--lnsy-edit-comment);\n}\n\n.cm-s-lnsy-edit span.cm-atom {\n  color: var(--lnsy-edit-atom);\n}\n\n.cm-s-lnsy-edit span.cm-number {\n  color: var(--lnsy-edit-number);\n}\n\n.cm-s-lnsy-edit span.cm-property,\n.cm-s-lnsy-edit span.cm-attribute {\n  color: var(--lnsy-edit-property);\n}\n\n.cm-s-lnsy-edit span.cm-keyword {\n  color: 