  --warning: yellow;
  --notice: green;


  --lnsy-edit-background: var(--bg-color);
  --lnsy-edit-foreground: var(--fg-color);
  --lnsy-edit-selection: var(--highlight-color);
  --lnsy-edit-neutral: var(--neutral);
  --lnsy-edit-gutter-background: var(--bg-color);
  --lnsy-edit-gutter-border: var(--bg-color);
  --lnsy-edit-guttermarker: var(--neutral-dark);
  --lnsy-edit-guttermarker-subtle: var(--neutral-dark);
  --lnsy-edit-linenumber: var(--neutral-dark);
  --lnsy-edit-cursor: var(--neutral-dark);
  --lnsy-edit-comment: var(--neutral);
  --lnsy-edit-atom: var(--neutral-light);
  --lnsy-edit-number: var(--neutral-light);
  --lnsy-edit-property: var(--neutral-light);
  --lnsy-edit-attribute: var(--neutral-light);
  --lnsy-edit-keyword: var(--fg-color);
  --lnsy-edit-string: var(--fg-color);
  --lnsy-edit-variable: var(--fg-color);
  --lnsy-edit-variable-2: var(--fg-color);
  --lnsy-edit-def: var(--neutral-light);
  --lnsy-edit-bracket: var(--neutral-light);
  --lnsy-edit-tag: var(--neutral-light);
  --lnsy-edit-link: var(--neutral-light);
  --lnsy-edit-error-background: var(--neutral-background);
  --lnsy-edit-error-foreground: var(--error);
  --lnsy-edit-activeline-background: var(--neutral-extra-dark);
  --lnsy-edit-matchingbracket-color: var(--bg-color) !important;
  --lnsy-edit-matchingbracket-background: var(--neutral);

}

